.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Custom styles for IT Automation Platform */
.ant-layout-sider {
  box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
}

.ant-menu-dark {
  background: #001529;
}

.ant-menu-dark .ant-menu-item-selected {
  background-color: #1890ff;
}

.ant-card {
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.ant-card-head {
  border-bottom: 1px solid #f0f0f0;
}

.ant-statistic-title {
  color: #666;
  font-size: 14px;
  margin-bottom: 4px;
}

.ant-statistic-content {
  color: #262626;
  font-size: 24px;
  font-weight: 600;
}

.ant-badge-status-dot {
  width: 8px;
  height: 8px;
}

.ant-timeline-item-content {
  margin-left: 18px;
}

.ant-progress-circle {
  margin: 0 auto;
}

/* Chat styles */
.chat-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
  background: #fafafa;
  border-radius: 8px;
  margin-bottom: 16px;
}

.chat-input {
  display: flex;
  gap: 8px;
}

.message-bubble {
  max-width: 70%;
  padding: 8px 12px;
  border-radius: 12px;
  margin-bottom: 8px;
  word-wrap: break-word;
}

.message-user {
  background: #1890ff;
  color: white;
  margin-left: auto;
  border-bottom-right-radius: 4px;
}

.message-bot {
  background: white;
  color: #262626;
  border: 1px solid #d9d9d9;
  border-bottom-left-radius: 4px;
}

/* Status indicators */
.status-open {
  color: #ff4d4f;
}

.status-in-progress {
  color: #1890ff;
}

.status-resolved {
  color: #52c41a;
}

.priority-high {
  color: #ff4d4f;
  font-weight: bold;
}

.priority-medium {
  color: #faad14;
  font-weight: bold;
}

.priority-low {
  color: #52c41a;
  font-weight: bold;
}

/* Dashboard specific styles */
.dashboard-card {
  height: 100%;
}

.metric-card {
  text-align: center;
  padding: 24px;
}

.chart-container {
  height: 300px;
  width: 100%;
}

/* Responsive design */
@media (max-width: 768px) {
  .ant-layout-sider {
    position: fixed;
    height: 100vh;
    z-index: 999;
  }
  
  .ant-layout-content {
    margin-left: 0 !important;
  }
  
  .message-bubble {
    max-width: 85%;
  }
}

/* Loading states */
.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}

/* Form styles */
.form-section {
  margin-bottom: 24px;
}

.form-section-title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 16px;
  color: #262626;
}

/* Table styles */
.ant-table-thead > tr > th {
  background: #fafafa;
  font-weight: 600;
}

.ant-table-tbody > tr:hover > td {
  background: #f5f5f5;
}

/* Button styles */
.action-button {
  margin-right: 8px;
  margin-bottom: 8px;
}

/* Alert styles */
.alert-high {
  border-left: 4px solid #ff4d4f;
}

.alert-medium {
  border-left: 4px solid #faad14;
}

.alert-low {
  border-left: 4px solid #52c41a;
}

/* Animation classes */
.fade-in {
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.slide-in {
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

/* Utility classes */
.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.mb-16 {
  margin-bottom: 16px;
}

.mt-16 {
  margin-top: 16px;
}

.p-16 {
  padding: 16px;
}

.full-width {
  width: 100%;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}